














import { Component, Vue } from 'vue-property-decorator';
import { EventEmitter } from 'events';

@Component({
  props: {
    isLoading: Boolean
  }
})
export default class Controls extends Vue {
  public onSend(e: Event) {
    this.$emit('send');
  }

  public onClear(e: Event) {
    this.$emit('clear');
  }

  public onSave(e: Event) {
    this.$emit('save');
  }
}
